<template>
  <section class="lists">
    <ul>
      <li v-for="shop in shops" @click="order">
        <figure>
          <!--<img src="/static/banner-1.jpg" alt="商家店铺" />-->
          <img v-bind:src="shop.shopUrl" width="350" height="234" />
          <figcaption>
            <h2>{{shop.shopName}}</h2>
            <p>{{shop.shopAddress}}</p>
          </figcaption>
        </figure>
        <!--<button type="button" @click="order">进入店铺</button>-->
      </li>
    </ul>
  </section>
</template>
<script>
  export default{
    created() {
      var that = this;
      $.ajax({
        url: 'http://localhost:8187/queryAllShop',
        type: 'post',
        success: function (data) {
          //that.$router.push({path: 'e'});
          console.log(data);
          that.shops = data;
          //debugger
        }
      });
    },
    data() {
      return {
        shops: '',
        isShow: true,
        curShop: ''
      }
    },
    methods: {
      order: function() {
        var that = this;
        $.ajax({
          url: 'http://localhost:8187/queryShop',
          type: 'post',
          data: {'id': 2},
          success: function (data) {
             that.$router.push({path: 'e'});
              console.log(data);
          }
        });
        //this.$router.push({path: 'f'});
      }
    }
  }
</script>
<style lang="less">
  .lists{
    width:100%;
    overflow: scroll;
  ul{
    list-style-type: none;
    margin:0;
    padding:0;
  li{
    width:100%;
    height:auto;
    margin: 5px 0;
    border:1px solid rgba(2, 12, 15, 0.89);
    position:relative;
  figure{
    margin:0;
  img{
    display: inline-block;
    width:30%;
    height: auto;
    margin:5px 2px 3px 3px;
    vertical-align: top;
  }
  figcaption{
    display: inline-block;
    width:60%;
   font-size:14px;
  h2{
    font-size: 0.8em;
  }
  }
  }
  button{
    position: absolute;
    right: 10px;
    bottom: 6px;
    border-radius: 5px;
    background-color: #f7edd9;
  }
  }
  }
  }
</style>



