<template>
  <div class="login">
    <h1>短信验证登录</h1>
    <ul class="message">
      <li>
        <input type="search" class="user-phone" name="user-phone" placeholder="请输入手机号" />
        <button class="obtain">获取验证码</button>
      </li>
      <li>
        <input type="search" class="verification" name="verification" placeholder="请输入短信中的验证码" />
      </li>
    </ul>
    <input class="submit" type="submit" value="登录" v-on:click="logined" />
  </div>
</template>
<script>
  export default {
    data() {
      return {
        name:'',
        pwd:'',
        isShow: true
      }
    },
    methods:{
      logined: function() {

        this.$router.push({ path: 'e'});
        this.isShow = !this.isShow;
        console.log("lwfyy" + this.isShow);
      },
      isLogin:function() {
        this.$http.get('http://localhost:3000/users?username='+this.name+'&password='+this.pwd).then((response) => {
          //这里在isLogin方法中先判断一下后台返回的是否为空值，如果不是然后提交后台返回的值。
          //注意这里是个难点，Vuex与Vue-Resource结合使用。
          if(response.body != null & response.body.length > 0){
            this.$store.commit('isLogin',response.body[0]);
            this.name=''
            this.pwd= ''
            this.$router.push({ path: 'restaurant' })
          }else{
            alert('请输入正确的用户名和验证码！！！');
            this.name=''
            this.pwd= ''
          }

        }).then((error)=> this.error = error)
      }
    }
  }
</script>

<style lang="less" scoped>
  html,body{
    margin: 0;
    padding: 0;
    font-size: 22px;

  }
  .login {
    width: 100%;
    height: 100%;
    background-color: #f9f9f9;
    h1{
      height: 36px;
      line-height: 36px;
      width: 100%;
      border-bottom: 1px solid #ecebeb;
      font-weight: 100;
      text-align: center;
      background-color: #ffffff;
      font-size: 20px;
    }
    ul{
      list-style: none;
      background-color: #ffffff;
      li{
        background-color: #ffffff;
        width: 100%;
        height: 40px;
        line-height:40px;
        border-bottom: 1px solid #dcdbdb;
        font-size: 14px;
        text-align: center;
      }
    }
  }
  .submit{
    background-color: #ffffff;
    width: 96%;
    height: 40px;
    margin-left: 2%;
    margin-top:15px;
    border-bottom: 1px solid #dcdbdb;
    font-size: 24px;
    text-align: center;
  }
  .login ul li input{
    width: 75%;
    height: 35px;
    line-height: 35px;
    border: none;
  }
  .login .verification{
    width:98%;
  }
  .obtain{
    border: none;
    background: none;
    height: 35px;
    display: inline-block;
    width: 22%;
    font-size: 14px;
  }
  .submit{
    background-color: #eae9e9;
    border-radius: 5px;
    color: #aba6a6;
    border: none;
  }
</style>



